/* import hallo fon*/
.jumbotron{
border-radius:10px !important;
box-shadow: 0 8px 16px -6px black;
background: rgb(195,218,223);
background: linear-gradient(-45deg,#ee7752, #e73c7e, #23a6d5, #23d5ab);
background-size: 400% 400%;
-webkit-animation: ond 15s ease infinite;
        animation: ond 15s ease infinite;
  }
  /*------------------------------------imagen circular----------------------------------*/
  .image-cropper {
    position: relative;
    width: 200px;
    height: 200px;
    overflow: hidden;
    border-radius: 50%;
  }
  .image-cropper img {
    width: 100%;
    height: auto;
  }
  /*------------------------------------cambiar la letra----------------------------------*/
  .font {
    font-family: 'PT Mono', monospace;
  }
  .animated {
    text-indent: 8px;

  }
  /*-----------------------------------animacion de la frase------------------------*/
  .animated:before {

    content: " Coding";
    -webkit-animation : topToBottom 5s infinite 0s;
            animation : topToBottom 5s infinite 0s;
  }

  @-webkit-keyframes topToBottom {
    0% {
      content: " Competitive Programming";
      color:purple;
    }
    25% {
      content: " Music";
      color:pink;
    }
    50% {
      content: " Coding";
      color:blue;
    }
    75% {
      content: " la Chikistrikis ♥️";
      color:#f3808c;
    }
    100% {
      content: " Competitive Programming";
      color:purple;
    }

  }

  @keyframes topToBottom {
    0% {
      content: " Competitive Programming";
      color:purple;
    }
    25% {
      content: " Music";
      color:pink;
    }
    50% {
      content: " Coding";
      color:blue;
    }
    75% {
      content: " la Chikistrikis ♥️";
      color:#f3808c;
    }
    100% {
      content: " Competitive Programming";
      color:purple;
    }

  }
  /*---------------------------------------animacion del jumbotron------------------------*/

  @-webkit-keyframes ond {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }

  @keyframes ond {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  /*----------------------------------------tamaño de la imagen del git-----------------------*/
  .img {
    width : 50px;
    height : 50px;
  }

  /*----------------------------------------texto bonito-------------------------*/
  .cute-text{
    -webkit-animation: ondi 15s ease infinite;
            animation: ondi 15s ease infinite;
  }
